.svg {
  position: relative;
}
.svg-rotate {
  position: absolute;
  top: -2px;
  left: 60px;
  z-index: -1;
  animation: rotation 24s infinite linear;
}



@keyframes rotation {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(-359deg);
  }
}
